<template>
    <div>
        <el-form :inline=true :model="form" ref="form">
            <el-row :gutter="5">
                <el-col :span="20">
                    <el-form-item label="学号:" label-width="68px">
                        <el-input v-model="form.studentid"></el-input>
                    </el-form-item>
                    <el-form-item label="姓名:" label-width="68px">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="班级" label-width="68px">
                        <el-select v-model="form.classname">
                            <el-option v-for="item in classList" :key="item.value" :label="item.label"
                                :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" size="mini" @click="searchStudent">搜索</el-button>
                        <el-button size="mini" @click="resetFields('form')">重置</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-table v-loading="tableDataLoading" :data="tableData" border>
            <el-table-column prop="studentid" label="学号" width="180">
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="phone" label="手机号">
            </el-table-column>
            <el-table-column prop="dorm" label="宿舍">
            </el-table-column>
            <el-table-column prop="address" label="家庭住址">
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
    export default {
        name: "userList",
        data() {
            return {
                form: {
                    studentid: "",
                    name: "",
                    classname: "",
                },
                classList: [],
                tableDataLoading: true,
                tableData: []
            }
        },
        mounted() {
            // 挂载
        },
        methods: {
            searchStudent() {
                this.tableDataLoading = true;
                var formData = new FormData();
                formData.append("studentid", this.form.studentid);
                formData.append("name", this.form.name);
                formData.append("classname", this.form.classname);
                axios
                .post("/user/searchStudent", formData)
                .then(res => {
                    this.tableDataLoading = false;
                    var result = res.data;
                    if( result.code == 200 ){
                        this.tableData = result.list;
                        return;
                    }
                    this.$message({
                        showClose: true,
                        message: '获取学生信息错误',
                        type: 'error'
                    });
                })
            },
            getStudentList() {
                axios
                .get("/user/StudentList")
                .then(res => {
                    this.tableDataLoading = false;
                    var result = res.data;
                    if( result.code == 200 ){
                        this.tableData = result.list;
                        return;
                    }
                    this.$message({
                        showClose: true,
                        message: '获取学生信息错误',
                        type: 'error'
                    });
                })
            },
            getClassNameList() {
                // 获取班级列表
                axios
                .get("/user/ClassNameList")
                .then(res => {
                    var result = res.data;
                    if( result.code == 200 ){
                        this.form.classList = result.list;
                        return;
                    }
                    this.$message({
                        showClose: true,
                        message: '获取班级信息失败',
                        type: 'error'
                    });
                })
                .catch(e => {
                    console.log(e)
                })
            },
            resetFields(form){
                this.form.studentid = ""
                this.form.name = ""
                this.form.classname = ""
                this.form.classList = []
            }
        }
    }
</script>

<style scoped>
    .el-input {
        width: 12rem;
    }
</style>